.rating {
  padding: 1.5rem 0;
  text-align: right;
  display: flex;

  @include desktop-breakpoint {
    padding: 1rem 0;
    margin-top: -1rem;
  }

  &__item {
    width: 0.95rem;
    height: 0.95rem;
    margin-left: 0.8rem;
    margin-bottom: 0.5rem;

    @include tablet-breakpoint {
      width: 1.5rem;
      height: 1.5rem;
    }
    @include desktop-breakpoint {
      width: 1.9rem;
      height: 1.9rem;
    }
  }
}
